import logo from './logo.svg';
import './App.css';
import Button from '@material-ui/core/Button';

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
} from "react-router-dom";

import Navigation from "./components/Navigation"; 
import Documentation from "./components/Documentation";
import Examples from "./components/Examples";


function App() {


  const openDownload = () => {
    window.open('https://github.com/csc309-winter-2021/js-library-chuajos2');
  }

  return (
    
    <div className="App">
      
      <Router>
        <Navigation/>

        <Switch> 
          <Route exact path="/examples">
            <Examples/> 
          </Route> 
          
          <Route exact path="/documentation">
            <Documentation/> 
          </Route> 
        </Switch> 

        <div className="App-content">
          <img src={logo} className="App-logo" alt="logo" />

          <h3>A better way to browse the internet.</h3>
      
          <div>  

            <Button component={Link} to="/Examples">
              Examples
            </Button>

            <Button component={Link} to="/Documentation">
              Documentation
            </Button>

            <Button onClick={openDownload}>
              Download
            </Button>

          </div> 
        </div>
      </Router> 

      <div className="App-footer"> 
        CSC309 Individual Project - Joshua Chua (chuajos2)
      </div> 
    </div>

  );
}

export default App;
